<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <div class="zd-common-fr">
        <DatePicker v-model="dateRangeArr" :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-common-input-h38 zd-cm-w220 zd-common-fl zd-common-mr10"
                    @on-change="changeDate"></DatePicker>
        <Select v-model="status" placeholder="全部状态"
                class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10" clearable>
          <!--<Option v-for="item in statusArr" :value="item.id" :key="item.id">{{ item.name }}</Option>-->
          <Option value="0">已确认</Option>
          <Option value="1">未确认</Option>
        </Select>
        <Input v-model="numOrName" placeholder="出库单号/物品名称" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16 zd-cm-tip-b">
      <Icon type="md-stats" class="zd-common-primary-c"/>
      {{ startDate+ "~"+
      endDate }} 期间，申领物品种类：<span
      class="zd-common-primary-c">{{ statisticsDetail.goodsNum }}
      </span>个，申领物品数量：<span class="zd-common-primary-c">{{ statisticsDetail.goodsCnt }}</span>个，申领物品金额：<span class="zd-common-primary-c">{{ statisticsDetail.totalPrice }}</span>元
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--查看-->
    <Modal v-model="viewModal" :mask-closable="false" :width="920" ref="print">
      <h3 slot="header" class="zd-common-modal-header no-print">
        <span>查看出库单</span>
      </h3>
      <div class="zd-common-h400">
        <vue-scroll>
          <div class="zd-common-clearFix">
            <span class="zd-common-fl zd-common-mr14">出库单号：<span class="zd-common-font-weight">{{ allDetail.num }}</span></span>
            <span class="zd-common-fl zd-common-mr14">出库日期：<span class="zd-common-font-weight">{{ allDetail.date }}</span> </span>
            <span class="zd-common-fl zd-common-mr14">出库类型：<span class="zd-common-font-weight">{{ allDetail.kindCode }}</span></span>
            <span class="zd-common-fl zd-common-mr14">部门：<span class="zd-common-font-weight">{{ allDetail.departmentName }}</span> </span>
            <span class="zd-common-fl zd-common-mr14">领用人：<span class="zd-common-font-weight">{{ allDetail.buyserName
            }}</span> </span>
          </div>
          <Table class="zd-common-mt10" border :columns="viewCols" :data="viewData" show-summary></Table>
          <div class="zd-common-mt10 zd-common-clearFix">
            <span class="zd-common-fl">制单人：{{ allDetail.userName }}</span>
            <span class="zd-common-fl zd-common-ml20">制单日期：{{ allDetail.productDate }}</span>
          </div>
          <div class="zd-common-mt10 zd-common-clearFix">
          <span class="zd-common-clearFix">
            <span class="zd-common-fl">备注：</span>
            <span class="zd-common-fl zd-cm-remark">{{ allDetail.note }}</span>
          </span>
          </div>
          <div class="zd-common-mt10 zd-common-clearFix zd-common-primary-c">
          <span class="zd-common-clearFix">
            <span class="zd-common-fl">确认时间：</span>
            <span class="zd-common-fl zd-cm-remark">{{ allDetail.productDate }}</span>
          </span>
          </div>
          <div class="zd-common-mt10 zd-common-clearFix zd-common-primary-c">
            <span class="zd-common-clearFix">
              <span class="zd-common-fl zd-common-cp" @click="downloadSignPic(allDetail.url)">查看签字图片</span>
              <!--<span class="zd-common-fl" style="width: 770px;">{{ allDetail.productDate }}</span>-->
            </span>
          </div>
        </vue-scroll>
      </div>
      <div slot="footer" class="zd-common-modal-footer no-print">
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--确认-->
    <Modal v-model="confirmModal" :mask-closable="false" :width="920" ref="print">
      <h3 slot="header" class="zd-common-modal-header no-print">
        <span>确认出库单物品</span>
      </h3>
      <div class="zd-cm-tip-b">
        <Icon type="md-stats" class="zd-common-primary-c"/>
        待确认物品来自以下出库单，点击确定将同步确认此出库单所有出库物品！
      </div>
      <div class="zd-common-h400 zd-common-mt10">
        <vue-scroll>
          <div class="zd-common-clearFix">
            <span class="zd-common-fl zd-common-mr14">出库单号：<span class="zd-common-font-weight">{{ allDetail.num }}</span></span>
            <span class="zd-common-fl zd-common-mr14">出库日期：<span class="zd-common-font-weight">{{ allDetail.date }}</span> </span>
            <span class="zd-common-fl zd-common-mr14">出库类型：<span class="zd-common-font-weight">{{ allDetail.kindCode }}</span></span>
            <span class="zd-common-fl zd-common-mr14">部门：<span class="zd-common-font-weight">{{ allDetail.departmentName }}</span> </span>
            <span class="zd-common-fl zd-common-mr14">领用人：<span class="zd-common-font-weight">{{ allDetail.buyserName
            }}</span> </span>
          </div>
          <Table class="zd-common-mt10" border :columns="viewCols" :data="viewData" show-summary></Table>
          <div class="zd-common-mt10 zd-common-clearFix">
            <span class="zd-common-fl">制单人：{{ allDetail.userName }}</span>
            <span class="zd-common-fl zd-common-ml20">制单日期：{{ allDetail.productDate }}</span>
          </div>
          <div class="zd-common-mt10 zd-common-clearFix">
            <span class="zd-common-clearFix">
              <span class="zd-common-fl">备注：</span>
              <span class="zd-common-fl zd-cm-remark">{{ allDetail.note }}</span>
            </span>
          </div>
          <Form class="zd-common-w200 zd-common-mt10" ref="signForm" :model="signForm" :rules="ruleValidate" :label-width="100" label-position="left">
            <FormItem prop="uploadId">
              <span slot="label" class="zd-common-fs14">签字图片：</span>
              <div class="demo-upload-list zd-common-w150" v-for="item in uploadList">
                <template v-if="item.status === 'finished'">
                  <img :src="item.url">
                  <div class="demo-upload-list-cover">
                    <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
                    <Icon type="ios-trash-outline" @click.native="handleRemove"></Icon>
                  </div>
                </template>
                <template v-else>
                  <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
                </template>
              </div>
              <Upload
                name="filedata"
                ref="upload"
                :show-upload-list="false"
                :format="['jpg','jpeg','png']"
                type="drag"
                :action="serverPath+'/component/attachment!saveUploadFile2.action'"
                :max-size="2048"
                :on-success="handleSuccess"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                class="zd-common-w150 zd-common-auto"
              >
                <Button type="primary" class="zd-common-w150">上传签字图片</Button>
              </Upload>
              <input type="text" v-model="signForm.uploadId" hidden/>
              <Modal v-model="visible" :width="60">
                <h3 slot="header" class="zd-common-modal-header">
                  <span>预览签字</span>
                </h3>
                <vue-scroll>
                  <div class="zd-common-font-size-base zd-common-lh30 zd-common-tc zd-common-h500">
                    <img :src="uploadPicUrl" v-if="visible" style="max-width: 100%">
                  </div>
                </vue-scroll>
                <div slot="footer" class="zd-common-modal-footer zd-common-pt0"></div>
              </Modal>
            </FormItem>
          </Form>
          <p class="zd-common-fs12 zd-common-des-font-c">允许上传png、jpg、jpeg格式图片</p>
        </vue-scroll>
      </div>
      <div slot="footer" class="zd-common-modal-footer no-print">
        <Button type="primary" class="zd-common-w100 zd-common-h32 zd-common-lh8" @click="signSubmit('signForm')" :loading="selectLoading">
          <span v-if="!selectLoading">确认</span>
          <span v-else>正在保存...</span>
        </Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import {utils} from '@comm/script/utils/index'

  export default {
    data() {
      return {
        //查询
        startDate: '',
        endDate: '',
        dateOptions: this.dateRange,
        statusArr: [],
        status: '',
        numOrName: '',
        dateRangeArr: [],
        //统计
        statisticsDetail: {},
        isNeedConfirm: false,
        //列表
        columns: [],
        data: [],
        pageSize: 10,
        pageNum: 1,
        total: 0,
        selectLoading: false,
        //详情
        viewModal: false,
        viewCols: [
          {
            type: 'index',
            title: '序号',
            align: 'center',
            width: 70
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'goodsInfoName',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'goodsKindName',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'size',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '数量',
            align: 'center',
            key: 'goodsNum'
          },
          {
            title: '价格',
            align: 'center',
            key: 'totalPrice'
          },
          {
            title: '金额',
            align: 'center',
            key: 'totalPrice'
          },
        ],
        viewData: [],
        allDetail: {},
        //确认
        confirmModal: false,
        //签字
        saveLoading: false,
        signId: '',
        signOrderNO: '',
        visible: false,
        uploadPicUrl: '',
        signForm: {
          uploadId: ""
        },
        ruleValidate: {
          uploadId: [
            {required: true, message: '请选择文件', trigger: 'blur'}
          ]
        },
        uploadList: []
      }
    },
    created() {
      const e = new Date();
      this.startDate = e.getFullYear() + "-01-01";
      this.endDate = e.getFullYear() + "-" + this.fillZero((e.getMonth() + 1)) + "-" + this.fillZero(e.getDate());
      this.dateRangeArr = [this.startDate, this.endDate];
      this.getProvideGoodsView(this.startDate, this.endDate);
    },
    methods: {
      //日期
      changeDate(arr) {
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //列表
      getProvideGoodsPage(pageNum, pageSize, startDate, endDate, status, num) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getProvideGoodsPage.action',
          data: {
            pageNum: pageNum,
            pageSize: pageSize,
            startDate: startDate,
            endDate: endDate,
            status: status,
            num: num
          },
        })
          .then((response) => {
            let aData = response.data;
            if (aData && aData.list.length > 0) {
              aData.list.map((item) => {
                this.data.push({
                  id: item.id,
                  outStockOrder: item.num,
                  outStockDate: item.date,
                  goodsName: item.name,
                  goodsCode: item.goodsCode,
                  classify: item.goodsKind,
                  brand: item.brand,
                  model: item.size,
                  unit: item.unit,
                  number: item.count,
                  amount: item.price,
                  status: item.price,
                  signFlag: item.signFlag
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getProvideGoodsPage(this.pageNum, this.pageSize, this.startDate, this.endDate, this.status, this.numOrName);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getProvideGoodsPage(this.pageNum, this.pageSize, this.startDate, this.endDate, this.status, this.numOrName);
      },
      //查询
      selectHandle() {
        this.selectLoading = true;
        this.getProvideGoodsPage(this.pageNum, this.pageSize, this.startDate, this.endDate, this.status, this.numOrName);
      },
      //统计
      getProvideGoodsView(startDate, endDate) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getProvideGoodsDetail.action',
          data: {
            startDate: startDate,
            endDate: endDate
          },
        })
          .then((response) => {
            let aData = response.data;
            this.statisticsDetail = aData;
            this.needSign = aData.needSign;
            if (aData.needSign) {
              this.columns = [
                {
                  type: 'index',
                  align: 'center',
                  title: '序号',
                  width: 65
                },
                {
                  title: '出库单号',
                  align: 'center',
                  key: 'outStockOrder',
                  width:140
                },
                {
                  title: '出库日期',
                  align: 'center',
                  key: 'outStockDate'
                },
                {
                  title: '物品名称',
                  align: 'center',
                  key: 'goodsName',
                },
                {
                  title: '物品编码',
                  align: 'center',
                  key: 'goodsCode',
                },
                {
                  title: '物品分类',
                  align: 'center',
                  key: 'classify',
                },
                {
                  title: '品牌',
                  align: 'center',
                  key: 'brand',
                },
                {
                  title: '规格/型号',
                  align: 'center',
                  key: 'model'
                },
                {
                  title: '单位',
                  align: 'center',
                  key: 'unit'
                },
                {
                  title: '数量',
                  align: 'center',
                  key: 'number'
                },
                {
                  title: '金额',
                  align: 'center',
                  key: 'amount'
                },
                {
                  title: '收货状态',
                  align: 'center',
                  key: 'status',
                  render: (h, params) => {
                    if (params.row.signFlag) {
                      return h('div', [
                        h('span', {}, '已确认')
                      ]);
                    } else {
                      return h('div', [
                        h('span', {}, '未确认')
                      ]);
                    }
                  }
                },
                {
                  title: '操作',
                  align: 'center',
                  key: 'action',
                  // width:240,
                  render: (h, params) => {
                    if (params.row.signFlag) {
                      return h('div', [
                        h('Button', {
                          props: {
                            type: 'primary',
                            ghost: true,
                          },
                          style: {
                            height: "28px",
                            padding: '0 15px 1px'
                          },
                          on: {
                            click: () => {
                              this.showDetail(params.row.id);
                            }
                          }
                        }, '查看')
                      ]);
                    } else {
                      return h('div', [
                        h('Button', {
                          props: {
                            type: 'primary',
                            ghost: true,
                          },
                          style: {
                            height: "28px",
                            padding: '0 15px 1px'
                          },
                          on: {
                            click: () => {
                              this.showConfirm(params.index);
                            }
                          }
                        }, '确认')
                      ]);
                    }
                  }
                }
              ]
            } else {
              this.columns = [
                {
                  type: 'index',
                  align: 'center',
                  title: '序号',
                  width: 65
                },
                {
                  title: '出库单号',
                  align: 'center',
                  key: 'outStockOrder'
                },
                {
                  title: '出库日期',
                  align: 'center',
                  key: 'outStockDate'
                },
                {
                  title: '物品名称',
                  align: 'center',
                  key: 'goodsName',
                },
                {
                  title: '物品编码',
                  align: 'center',
                  key: 'goodsCode',
                },
                {
                  title: '物品分类',
                  align: 'center',
                  key: 'classify',
                },
                {
                  title: '品牌',
                  align: 'center',
                  key: 'brand',
                },
                {
                  title: '规格/型号',
                  align: 'center',
                  key: 'model'
                },
                {
                  title: '单位',
                  align: 'center',
                  key: 'unit'
                },
                {
                  title: '数量',
                  align: 'center',
                  key: 'number'
                },
                {
                  title: '金额',
                  align: 'center',
                  key: 'amount'
                }
              ]
            }
            this.getProvideGoodsPage(this.pageNum, this.pageSize, this.startDate, this.endDate, this.status, this.numOrName);
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //获取详情
      showDetail(id) {
        this.viewModal = true;
        this.getOutWarehouseRecordInfo(id);
      },
      getOutWarehouseRecordInfo(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getOutWarehouseRecordInfo.action',
          data: {
            id: id
          },
        })
          .then((response) => {
            const aData = response.data;
            this.allDetail = aData;
            this.viewData = aData.goodsList;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      downloadSignPic(url) {
       location.href = this.serverPath + url;
      },
      //确认
      showConfirm(index) {
        this.signForm.uploadId = '';
        this.confirmModal = true;
        this.signOrderNO = this.data[index].putInOrder;
        this.signId = this.data[index].id;
        this.uploadList = [];
        this.getOutWarehouseRecordInfo(this.signId);
      },
      //上传签字
      handleSuccess(res, file) {
        this.signForm.uploadId = res.id;
        file.url = this.serverPath + "/sr/storeroomData!showTempPic.action?attachmentId=" + res.id;
        this.uploadList = [file];
        this.$refs['signForm'].validate();
      },
      handleFormatError(file) {
        this.postWarningTip('图片格式错误');
      },
      handleMaxSize(file) {
        this.postWarningTip('最多上传2M大小的图片');
      },
      handleView(pic) {
        this.uploadPicUrl = pic;
        this.visible = true;
      },
      handleRemove() {
        this.uploadList = [];
        this.signForm.uploadId = '';
      },
      signSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.confirmModal = false;
            this.saveLoading = true;
            this.signOutWarehouseRecord(this.signId, this.signForm.uploadId);
          }
        })
      },
      signOutWarehouseRecord(id, attIds) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!signOutWarehouseRecord.action',
          data: {
            id: id,
            attIds: attIds
          }
        })
          .then((response) => {
            this.saveLoading = false;
            this.getProvideGoodsPage(this.pageNum, this.pageSize, this.startDate, this.endDate, this.status, this.numOrName);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
    },
    mounted() {

    }
  }
</script>
<style lang="less">
  .ivu-table .demo-table-info-row td {
    background-color: rgba(28, 144, 243, 0.1);
  }
</style>



